
'use client';

import * as React from 'react';
import { congressResources } from '@/lib/learning-resources';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import Link from 'next/link';
import { FileText, Video } from 'lucide-react';

export default function CongressLearningPage() {
  const events = Array.from(new Set(congressResources.map(r => r.event)));

  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold font-headline">Student Congress Resources</h1>
        <p className="text-muted-foreground">
          Learn about parliamentary procedure, legislation, and debate strategy.
        </p>
      </div>

      <Accordion type="multiple" defaultValue={events} className="w-full space-y-4">
        {events.map(event => (
          <AccordionItem value={event} key={event} className="border rounded-lg">
            <AccordionTrigger className="p-4 hover:no-underline font-headline text-lg">{event}</AccordionTrigger>
            <AccordionContent className="p-4 pt-0 border-t">
              <div className="divide-y">
                {congressResources.filter(r => r.event === event).map(resource => (
                  <Link key={resource.id} href={`/dashboard/learning-hub/resource/${resource.id}`} className="block p-3 -mx-3 hover:bg-muted/50 rounded-md">
                      <div className="flex items-center gap-4">
                          {resource.type === 'video' ? <Video className="h-5 w-5 text-primary" /> : <FileText className="h-5 w-5 text-primary" />}
                          <div>
                            <p className="font-medium">{resource.title}</p>
                            <p className="text-sm text-muted-foreground line-clamp-1">{resource.description}</p>
                          </div>
                      </div>
                  </Link>
                ))}
              </div>
            </AccordionContent>
          </AccordionItem>
        ))}
      </Accordion>
    </div>
  );
}
